<template>
<div>
  <h2></h2>
  <el-button @click="toPageByName('Main')"></el-button>
  <div class="container">
    hexo(hexo)
    <el-input v-model="source"></el-input>
    
    <el-input v-model="categories"></el-input>
    
    <el-input v-model="tags"></el-input>
  </div>

  <center>
    <el-button @click="save"></el-button>
  </center>
</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { filterForData } from '@/assets/utils'

export default {
  name: 'Config',

  data () {
    return {
      source: '',
      categories: '',
      tags: '',
    }
  },

  computed: {
    ...mapGetters([
      'hexoSource',
      'categoriesOption',
      'tagsOption'
    ]),
  },

  methods: {
    ...mapActions([
      'getConfig',
      'saveConifg',
    ]),

    toPageByName (name) {
      this.$router.push({ name })
    },

    save () {
      if (
        this.source === '' ||
        this.categories === '' ||
        this.tags === ''
      ) {
        this.$message.error('')
        return
      }

      const data = {
        source: this.source,
        categories: filterForData(this.categories),
        tags: filterForData(this.tags),
      }

      this.saveConifg(data).then(() => {
        this.updateConfig()
        this.$message({
          message: '',
          type: 'success',
        })
      })
    },

    updateConfig () {
      this.getConfig().then(() => {
        this.source = this.hexoSource
        this.categories = this.categoriesOption.join(',')
        this.tags = this.tagsOption.join(',')
      })
    }
  },

  created () {
    this.updateConfig()
  }
}
</script>

<style scoped>
.container {
  margin: 0 auto;
  width: 500px;
}
</style>



